.App {
  text-align: center;
}

body{
  background-color: #282c34;
  min-height:  100vmin;;
}

.App-logo {
  height: 20vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.SearchForm-wrapper{
  display: flex;
  justify-content: center;
}



.movieList{
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 10px;
}

.movie{
  width: 300px;
  margin: 5px;
  background: #fff;
}

.card-detail{
  width: 50%;
  margin: 0 auto;
  padding: 20px;
}

.card-detail button{
  margin: 20px;
}

.movie p{
  color: gray; 
}

.movie:hover p{
  color: #3298dc;
}

.App-header {
  
  min-height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.searchForm-wrapper{
  display: flex;
  justify-content: center;
}